body {
  line-height: 1.5;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
ul,
a,
p {
  margin-bottom: 0;
  line-height: 1.5;
}
ul {
  padding-left: 0;
}
li {
  list-style: none;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}
